.button {
  width: 180px;
  height: 45px;
  font-family: "Roboto Condensed", Arial, sans-serif;
  font-size: 14px;
  line-height: 16px;
  color: $main-color;
  letter-spacing: 0.05em;
  background-color: rgba(27, 45, 55, 0);
  border: 1px solid $main-color;
  box-sizing: border-box;
  border-radius: 5px;
  text-transform: uppercase;
  cursor: pointer;
}

.button--info {
  margin-left: 2.6vw;
  margin-top: 3px;
}

.button--promo {
  display: block;
  font-size: 16px;
  line-height: 19px;
  width: 407px;
  height: 55px;
  text-decoration: none;
  text-align: center;
  margin-left: 94px;
  margin-bottom: 130px;
  padding-top: 17px;
  border: none;
  background-color: $complementary-color;
}

.button--promo-mobile {
  display: none;
}

.button--about {
  display: block;
  color: $complementary-color;
  border: 1px solid $complementary-color;
  text-align: center;
  padding-top: 14px;
  text-decoration: none;
  // margin-left: 177px;
}

@media (max-width: $tablet-width) {

  .button--info {
    display: none;
  }

  .button--promo {
    width: 379px;
    margin-left: 113px;
    margin-bottom: 0;
  }

  // .button--about {
  //   margin-left: 190px;
  // }
}

@media (max-width: $mobile-width) {

  .button--promo {
    width: 90vw;
    margin: 0;
  }

  .button--promo-mobile {
    display: inline;
  }

  .button--promo-desktop {
    display: none;
  }
}
